	module.exports = {"data":{"site":{"siteMetadata":{"title":"signalwerk"}},"markdownRemark":{"html":"<h1 id=\"lato\"><a href=\"#lato\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Lato</h1>\n<p><small>Pascale Anderegg</small></p>\n<div class=\"col1to12\">\n<p><img src=\"/IAD2017.schriftmuster/Lato-0d97c2693e41d1649c8759b3f5367fa6.svg\" alt=\"Lato\"></p>\n</div>\n<ol>\n<li>Zweistöckiges a mit halb-rundem Abstrich, verjüngt</li>\n<li>Oberkant Oberlänge überragt Versalhöhe nur minim</li>\n<li>eher hohe x-Höhe</li>\n<li>dreistöckiges g mit halb-runder Fahne, verjüngt</li>\n<li>rechtwinkliger Abstrich bei f  </li>\n<li>dünner werdender Überlauf (Verjüngung)  </li>\n</ol>\n<h2 id=\"design\"><a href=\"#design\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Design</h2>\n<p>Lato ist eine serifenlose und freie Schriftart von Łukasz Dziedzic und hat ihren Ursprung in Polen. Sie wurde im Sommer 2010 entwickelt und im Dezember 2010 unter der SIL Open Font License veröffentlicht. Die Schriftart hat ihren Namen von dem polnischen Wort Lato, welches für Sommer steht. Ursprünglich war die Lato für einen grösseren Kunden als Corporate Font-Set konzipiert worden. Dieser Kunde entschied sich aber am Ende für eine andere Stilrichtung und die Familie wurde für die Öffentlichkeit zugänglich. Bei der Entwicklung wurde Łukasz Dziedzic von Google unterstützt. Lato ist daher auch in der Google Font Bibliothek zu finden, wo sie zu den beliebtesten Schriftarten gehört.</p>\n<p>Mit den 10 Schnitten wollte der Designer eine Schrift entwerfen, die für Body Text eine transparente Erscheinung aufweist. Ihre besonderen Merkmale sollen in grösseren Schriftgraden und beim genauerem Betrachten sichtbar werden. Er benutzte vor allem in den Oberlängen klassische Proportionen um der Schriftfamilie eine gewohnte Harmonie und Eleganz zu verleihen.</p>\n<h2 id=\"designer\"><a href=\"#designer\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Designer</h2>\n<p>Łukasz Dziedzic</p>\n<h4 id=\"quellen\"><a href=\"#quellen\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Quellen</h4>\n<ol>\n<li><a href=\"http://de.wikipedia.org/wiki/Lato\">Wikipedia</a></li>\n<li><a href=\"http://latofonts.com/\">Lato</a></li>\n<li><a href=\"http://fonts.google.com/specimen/Lato\">Google Font Bibliothek</a></li>\n</ol>","frontmatter":{"date":"April 05, 2018","title":"Lato"}}},"pathContext":{"slug":"/fonts/pascale/"}}

